@import url('https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap');



@keyframes appears {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes left {
    from{
        transform: translateX(0%);
    }
    to{
        transform: translateX(-50%);
    }
}
@keyframes right {
    from{
        transform: translateX(0%);
    }
    to{
        transform: translateX(50%);
    }
}

@keyframes pulse {
    0% {
      -moz-box-shadow: 0 0 0 0 hsla(0, 100%, 100%, 0.4);
      box-shadow: 0 0 0 0 hsla(0, 100%, 100%, 0.4);
    }
    70% {
        -moz-box-shadow: 0 0 0 40px hsla(39, 89%, 49%, 0);
        box-shadow: 0 0 0 40px hsla(39, 89%, 49%, 0);
    }
    100% {
        -moz-box-shadow: 0 0 0 0 hsla(39, 89%, 49%, 0);
        box-shadow: 0 0 0 0 hsla(39, 89%, 49%, 0);
    }
  }

:root{
    --font-family: 'Barlow Semi Condensed';
    --scissors: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    --paper: linear-gradient(hsl(230, 89%, 62%) , hsl(230, 89%, 65%));
    --rock: linear-gradient(hsl(349, 71%, 52%) , hsl(349, 70%, 56%));
    --lizard: linear-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
    --spock: linear-gradient(hsl(189, 59%, 53%) , hsl(189, 58%, 57%));
    --dark: hsl(229, 25%, 31%);
    --score: hsl(229, 64%, 46%);
    --head: hsl(217, 16%, 45%);
    --bg: radial-gradient(circle at top, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
    --size: 200px;
    --round: calc(0.5*var(--size));
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    cursor: default;
    
}

/* Start styling the attribution */
.attribution { 
    display: none;
    position: fixed;
    bottom: 0rem;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0.7rem; 
    line-height: 1.6; 
    text-align: center; 
    color:hsl(220, 15%, 55%);
    width: 100%;
}
.attribution a { 
    color: var(--Soft-blue);
}
.attribution a:hover{
    color: var(--Cyan);
    transition: color .3s;
}

/* Screen reader class to hide the content visually */
.sr-only {
    position: absolute !important;
    margin: -1px !important;
    -webkit-clip-path: inset(50%) !important;
            clip-path: inset(50%) !important;
    border: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    overflow: hidden !important;
    white-space: nowrap !important;
}

/*General and page initial setup*/
html{
    font-size: 100%;
    font-family: var(--font-family), sans-serif;
    scroll-behavior: smooth;
}

img{
    max-width: 100%;
    height: auto;
}

main{
    background-image: var(--bg);

}

.container{
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    padding: 2rem 2rem 3.5rem;
}

.head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 6rem;
    border: 3px solid var(--head);
    border-radius: 6px;
    padding: 0.6rem 0.6rem 0.6rem 1.5rem;

    .logo{
        transform: scale(0.45) translateX(-65%);
    }

    .score{
        background-color: hsl(0,0%, 95%);
        width: 5rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 4px;

        p{
            font-size: 10px;
            color: var(--score);
            font-weight: 700;
            letter-spacing: 1px;

        }

        span{
            font-size: 38px;
            font-weight: 700;
            color: hsl(0,0%, 35%);
            line-height: 38px;
        }
    }
}

.home{
    width: var(--size);
    height:var(--size);
    margin: 0rem auto 2rem;
    background-image: url('../images/bg-pentagon.svg');
    background-position: center;
    background-size: var(--size);
    background-repeat: no-repeat;

    position: relative;

    .color{
        cursor: pointer;
        position: absolute;

        display: flex;
        align-items: center;
        justify-content: center;

        
        border-radius: 50%;

        height: calc(0.5*var(--size));
        width: calc(0.5*var(--size));
     
        background-clip: padding-box;


       

        .white{
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: hsl(0,0%, 90%);
            border-radius: 50%;
            width: calc(100% - 24px);
            height: calc(100% - 24px);
            box-shadow: inset 0px 4px 0px #C6BEBE;

            svg{
                cursor: pointer;
                scale: 0.7;
                pointer-events: none;
            }
        }
    }

    
}

.scissor{
    top: -50px;
    left: 50px;
    background-image: var(--scissors);
    border-bottom: 4px inset #C46A0E;
}

.paper{
    top: 30px;
    right: -55px;
    background-image: var(--paper);
    border-bottom: 4px inset #2845C1;
}

.rock{
    bottom: -55px;
    right: -15px;
    background-image: var(--rock);
    border-bottom: 4px inset #B11A39;
}

.lizard{
    bottom: -55px;
    left: -15px;
    background-image: var(--lizard);
    border-bottom: 4px inset #6538AD;
}

.spock{
    top: 30px;
    left: -55px;
    background-image: var(--spock);
    border-bottom: 4px inset #2D8CAA;
}

.rules-container{
    cursor: pointer;
    width: 8rem;
    min-height: 2.5rem;
    border: 1.3px solid hsl(0,0%, 100%);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;

    p{
        cursor: pointer;
        color: white;
        font-size: 16px;
        letter-spacing: 2px;
    }

    
}

.active{
    top: 0; left: 0; right: 0; bottom: 0;
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: 5;
    flex-direction: column;
    justify-content: space-between;
    background-color: hsl(0,0%, 97%);
    padding: 6rem 0 4rem;

    p{
        color: var(--dark);
        font-size: 32px;
        letter-spacing: 0px;
        font-weight: 700;
        
    }

    .rules{
        scale: 0.95;
        margin-bottom: 2rem;
    }


}

.ongame{
    background-image: none;
}

.home{
    .played{
        height: calc(0.65*var(--size));
        width: calc(0.65*var(--size));
        left: -55px;
        right: unset;
        bottom: unset;
        top: -45px;
        border-width: 6px;

        
        
        .white{
            width: calc(100% - 30px);
            height: calc(100% - 30px);
            border-width: 6px;
            svg{
                scale: 0.8;
            }
        }
    
        &::after{
            content: 'YOU PICKED';
            position: absolute;
            color: white;
            font-size: 16px;
            letter-spacing: 1.5px;
            bottom: -3rem;
        }
    }
    
    .bank{
        height: calc(0.65*var(--size));
        width: calc(0.65*var(--size));
        left: unset;
        right: -55px;
        bottom: unset;
        top: -45px;
        border-width: 6px;

        .white{
            width: calc(100% - 30px);
            height: calc(100% - 30px);
            
            box-shadow: inset 0px 6px 0px #C6BEBE;

            svg{
                scale: 0.8;
            }
        }
        &::after{
            
            content: 'THE HOUSE PICKED';
            position: absolute;
            text-align: center;
            color: white;
            width: 120%;
            font-size: 16px;
            letter-spacing: 1.5px;
            bottom: -3rem;
        }
    }

    .bot{
        &::after{
            bottom: calc(-3rem + 6px);
        }
    }
}

.unset{
    opacity: 0.3;
    filter: brightness(0) saturate(100%) invert(10%) sepia(44%) saturate(1133%) hue-rotate(202deg) brightness(90%) contrast(102%);
}

.none{
    display: none;
}

.result{
    position: absolute;
    width: 100%;
    bottom: -16.5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    
    animation: appears 1s ease 2.2s forwards;
    
    span{
        width: 120%;
        text-align: center;
        color: white;
        font-size: 54px;
        font-weight: 700;
        letter-spacing: 2px;
        padding-bottom: 1rem;
    }

    .play{
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 14rem;
        height: 3rem;
        border-radius: 8px;
        background-color: white;
        color: var(--dark);
        letter-spacing: 2px;
        font-size: 18px;
        font-weight: 600;
        p{
            cursor: pointer;
        }
    }
}

.winner{
    animation: pulse 2s infinite 2s;
}



@media screen and (min-width: 1024px){
    :root{
        --size: 350px;
        
    }
    .container{
        justify-content: flex-start;
       
        padding: 6vh 0;
    }
    .head{
        width: 44rem;
        border-radius: 1rem;
        height: 9.5rem;
        padding: 1rem 1.5rem 1rem 1.8rem;
        .logo{
            transform: scale(1) translateX(0%) translateY(2px);
        }
        .score{
            width: 9.5rem;
            border-radius: 8px;

            p{
                font-size: 18px;
            }

            span{
                font-size: 64px;
                line-height: 64px;
            }
        }
    }

    .middle{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 100%;
        flex-grow: 1;
    }

    .home{

        background-size: 330px 300px;
        margin-bottom: 0;
        .color{
            width: calc(0.40*var(--size));
            height: calc(0.40*var(--size));
            border-width: 6px;
            .white{
                width: calc(100% - 30px);
                height: calc(100% - 30px);
                box-shadow: inset 0px 6px 0px #C6BEBE;

                svg{
                    scale: 1;
                }
            }
        }

        .scissor{
            top: -45px;
            left: 102px;
            background-image: var(--scissors);
            
            
        }
    
        .paper{
            top: 80px;
            right: -55px;
            background-image: var(--paper);
          
        }
    
        .rock{
            bottom: -55px;
            right: 10px;
            background-image: var(--rock);
   
        }
    
        .lizard{
            bottom: -55px;
            left: 10px;
            background-image: var(--lizard);
    
        }
    
        .spock{
            top: 80px;
            left: -55px;
            background-image: var(--spock);

        }
    }

    .rules-container{
        position: fixed;
        bottom: 2rem;
        right: 2rem;
        
    }

    .active{
        cursor: default;
        top: calc(50% - 230px); left: calc(50% - 200px); right: 0; bottom: 0;
        width: 400px;
        height: fit-content;
   
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto;
        gap: 1rem;
        z-index: 4;
        align-content: center;
        background-color: hsl(0,0%, 97%);
        padding: 2rem 2rem 3rem;
        
        p{
            cursor: default;
            grid-column: 1/2;
            color: var(--dark);
            font-size: 32px;
            letter-spacing: 0px;
            font-weight: 700;
            
        }
    
        .rules{
            cursor: default;
            grid-column: 1/3;
            grid-row: 2/3;
            justify-self: center;
            scale: 1;
            margin-bottom: 0rem;
        }

        
        
        .close{
            cursor: pointer;
            justify-self: flex-end;

            *{
                cursor: pointer;
            }
        }

    }

    .cache{
        position: fixed;
        top: 0; left: 0; right: 0; bottom: 0;
        z-index: 3;
        width: 100vw;
        height: 100vh;
        background-color: hsla(0,0%, 0%, 0.5);
    }

    .home{
        .played{
            height: calc(0.8*var(--size));
            width: calc(0.8*var(--size));
            left: -155px;
            right: unset;
            bottom: unset;
            top: 70px;
            border-width: 12px;
            
            .white{
                width: calc(100% - 60px);
                height: calc(100% - 60px);
                box-shadow: inset 0px 12px 0px #C6BEBE;
                svg{
                    scale: 1.8;
                }
            }
        
            &::after{
                content: 'YOU PICKED';
                position: absolute;
                color: white;
                font-size: 24px;
                letter-spacing: 2.5px;
                bottom: unset;
                top: -5.5rem;
            }
        }
        
        .bank{
            height: calc(0.8*var(--size));
            width: calc(0.8*var(--size));
            left: unset;
            right: -155px;
            bottom: unset;
            top: 70px;
            border-width: 12px;
    
            .white{
                width: calc(100% - 60px);
                height: calc(100% - 60px);
                box-shadow: inset 0px 12px 0px #C6BEBE;
    
                svg{
                    scale: 1.8;
                }
            }
            &::after{
                
                content: 'THE HOUSE PICKED';
                position: absolute;
                text-align: center;
                font-size: 24px;
                letter-spacing: 2.5px;
                
                bottom: unset;
                top: -5.5rem;
            }
        }
    
        .bot{
            &::after{
                bottom: calc(-3rem + 6px);
            }
        }
    }

    .result{
        bottom: 22%;
    }

    .left{
        transform: translateX(-50%);
        transition: transform 1s ease 2s;
    }

    .right{
        transform: translateX(50%);
        transition: transform 1s ease 1s;
    }
}

